*{
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  transition: .3 ease;
}

/* Colours & Styles - Default config */
:root{
  --PrimaryColor: hsl(94, 59%, 35%);
  --HoverColor: #bdf094;
  --paleGreen: hsl(96, 75%, 89%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --textColor: hsl(240, 1%, 48%);
  --bgColor: hsl(220, 10%, 94%);
  --greyText: rgb(190, 190, 190);
  --inputColor: hsl(330, 12%, 97%);

  /* Font & typography */
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.5rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: .938rem;
  --smallFontSize: .813rem;
  --smallestFontSize: .75rem;
}

/* Default styles */
html{
  font-size: 90%;
}

a{
  text-decoration: none;
}

li{
  list-style: none;
}

.icon{
  font-size: 1.25rem;
  font-size: var(--h2FontSize);
}

img{
  width: 100%;
  height: auto;
}

.flex{
  display: flex;
  align-items: center;
}

.grid{
  display: grid;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.btn{
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 8px;
  box-shadow: 0 2px 4px hsl(330, 12%, 97%);
  box-shadow: 0 2px 4px var(--inputColor);
  padding: .8rem 1rem;
  color: hsl(240, 1%, 48%);
  color: var(--textColor);
}

.btn .icon{
  margin-left: .5rem;
}

.btn:hover{
  background: hsl(96, 75%, 89%);
  background: var(--paleGreen);
  color: hsl(94, 59%, 35%);
  color: var(--PrimaryColor);
}

body{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 1rem;
}

.container{
  position: relative;
  width: 95vw;
  height: 95vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: hsl(220, 10%, 94%);
  background: var(--bgColor);
  overflow: hidden;
  border-radius: 1rem;
  box-shadow: 2px 2px 8px rgb(190, 190, 190);
  box-shadow: 2px 2px 8px var(--greyText);
}

::-webkit-scrollbar{
  display: none;
}

/* Media Queries ========> */
@media screen and (max-width: 990px) {
  .heading{
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 1rem;
    gap: 1rem;
  }
}

@media screen and (max-width: 918px) {
  .heading{
      flex-direction: row;
      align-items: center;
      grid-gap: 1rem;
      gap: 1rem;
  }
}

@media screen and (max-width: 500px) {
  .heading{
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 1rem;
    gap: 1rem;
  }
}
.mainContent {
  width: 82%;
  height: 100%;
  padding: 2rem;
  overflow: auto;
}
.mainContent .bottom {
  margin-top: 2rem;
  grid-gap: 2rem;
  gap: 2rem;
  align-items: flex-start;
}

@media screen and (max-width: 918px) {
  .bottom {
    flex-direction: column;
  }
}/*# sourceMappingURL=bodyco.css.map */
